//////////////////////
// slider
//////////////////////

// slides

.slides {
  // autoHeight animations
  .transition-default.out();
  transition-duration: @time--big;
}

.slides_inner {
  // dragger animations
  &:not(.dragging) { // not when dragging
    .transition-default.out();
    transition-duration: @time--big;
  }
}

// pagination

.slider_pagination {
  .list();
  .list-space(small);
  .align-items--center();
  .justify-content--center();
  .padding(top, medium);
  // content
  [data-xt-pag] {
    .btn();
    .btn--tiny();
    &@{btn--design}, > span {
      border-radius: 2rem;
    }
    > span {
      padding: .5em 1.15em;
      min-height: 2.2rem;
    }
    &.active {
      > span {
        padding: .5em 2em;
      }
    }
  }
}

// navigation

.slider {
  [data-xt-nav] {
    .btn();
    .btn--tiny();
    &@{btn--design}, > span {
      border-radius: 2rem;
    }
    > span {
      padding: .5em 1.15em;
      min-height: 2.2rem;
    }
    [class^='icon-'], [class*=' icon-'] {
      .icon--small();
    }
  }
}
